import { Box, Typography } from "@mui/material";
import { makeStyles } from '@mui/styles';
import theme from "../theme";

const useStyles = makeStyles((_) => ({
    root: {
        height: "500px",
        padding: "75px 6vw 0px 6vw",
        display: "flex",
        alignItems: "center",
        position: "relative",
        '&::after': {
            content: '""',
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            
            zIndex: -1,
        },
        [theme.breakpoints.down('md')]: {
            height: "auto",
            justifyContent: "center"
        }
    },
    descriptionContainer: {
        width:"65%",
        [theme.breakpoints.down('md')]: {
            textAlign: "center",
            alignItems: "center",
            width:"100%",

        },
    },
    imageContainer: {
        width: "350px",
        height: "350px",
        position: "relative",
        "& svg": {
            width: "100% !important",
            height: "100% !important",
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%,-50%)",
        },
        [theme.breakpoints.down('md')]: {
            width: "300px",
            height: "300px",
            "& svg": {
                paddingTop: "25px",
                width: "100% !important",
                height: "100% !important",
            },
        }
    }
}));

function Intro({ title, desc, imagePath }) {
    const classes = useStyles();
    const formattedTitle = title.split('\n').map((line, index) => (
        <div key={index}>{line}</div>
    ));
    return (
        <Box
            component="div"
            className={classes.root + " slideDownAnimation animated"}
            id="About"
            style={{
                background: `url(${imagePath}) no-repeat center center`,
                backgroundSize: "cover",
                
            }}
        >
            <Box component="div" className={classes.descriptionContainer}>
                <Typography variant="h1">
                    {formattedTitle}
                </Typography>
                <Typography variant="h6" gutterBottom>
                    {desc}
                </Typography>
            </Box>
        </Box>
    );
}

export default Intro;
