import React from 'react';
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles((theme) => ({
    svgButton: {
        display: "inline-block",
        height: "64px",
        width: "64px",
        borderRadius: "50% !important",
        position: "relative",
        "& svg": {
            position: "absolute",
            left: "50%",
            top: "50%",
            height: "32px",
            width: "32px",
            transform: "translate(-50%,-50%)"
        },
        "&:hover": {
            cursor: "pointer",
        }
    },
}));

export function SvgButton(props) {
    const { id,className, onClick, children, idleColor, activeColor="white" } = props;
    const [isHovered, setIsHovered] = React.useState(false);
    const classes = useStyles();
    const handleMouseEnter = () => {
        setIsHovered(true);
    };

    const handleMouseLeave = () => {
        setIsHovered(false);
    };

    const buttonStyle = {
        backgroundColor:  idleColor,
        color: activeColor,
        cursor: "pointer",
        transition: "background-color 0.3s, color 0.3s",
        position: "relative",
    };

    if (isHovered) {
        buttonStyle.backgroundColor =activeColor;
        buttonStyle.color =idleColor;
    }

    return (
        <div
            id={id}
            style={buttonStyle}
            className={className + " " + classes.svgButton} 
            onClick={onClick}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
        >
            {children}
        </div>
    );
}
