import React, { useEffect } from "react";
import About from "./components/about";
import Contact from "./components/contact";
import Footer from "./components/footer";
import Header from "./components/header";
import { useMemo, useLayoutEffect } from "react";
import { makeStyles } from '@mui/styles';
import Other from "./components/other";
import { HashRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import SummaryAndReview from "./components/summaryAndReview";

import ExplorationLab from "./components/explorationLab";
import DiscoverMe from "./components/discoverMe";
import PreloadImages from "./PreloadImages";
import Milestone from "./components/milestone";

const useStyles = makeStyles((_) => ({
  slideDownAnimationBefore: {
    opacity: "0",
    transform: "translate(0,-75px)"
  },
  slideDownAnimationAfter: {
    opacity: "1",
    transition: "opacity 0.7s, transform 0.7s",
    transitionTimingFunction: "ease-out",
    transform: "translate(0,0px)"
  },
  slideRightAnimationBefore: {
    opacity: "0",
    transform: "translate(-75px,0px)"
  },
  slideRightAnimationAfter: {
    opacity: "1",
    transition: "opacity 0.7s, transform 0.7s",
    transitionTimingFunction: "ease-out",
    transform: "translate(0,0px)"
  },
}));

function App() {
  const classes = useStyles();  

  return (
    <Router >
      <PreloadImages/>
      <Header />
      <Routes>
        <Route path="/" exact element={<About />} />
        <Route path="/other" element={<Other />} />
        <Route path="/summaryandreview" element={<SummaryAndReview />} />
        <Route path="/explorationlab" element={<ExplorationLab />} />
        <Route path="/milestone" element={<Milestone />} />
        <Route path="/discoverme" element={<DiscoverMe />} />
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
      <Contact />
      <Footer />
    </Router>
  );
}

export default App;
