import { Grid, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { Carousel } from "../utils/carousels";
import { useSelector } from "react-redux";
import theme from "../theme";
import useMediaQuery from "@mui/material/useMediaQuery";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "75px 6vw 50px 6vw",
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },
    titleSubText: {
        paddingBottom: "25px",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },
    titleSubTextPre: {
        color: "grey !important",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },

    cardRoot: {
        border: "2px solid #62656c",
        borderRadius: "25px",
        height: "400px",
        padding: "25px",
        paddingTop: "50px",
        flexShrink: "0",
        display: "inline-block",
        whiteSpace: "normal",
        verticalAlign: "top",
    },
    cardImage: {
        width: "200px",
        height: "200px",
        display: "block",
        marginLeft: "auto",
        marginRight: "auto",
        marginBottom: "25px",
        objectFit: "contain"
    },
    cardTitle: {
        textAlign: "center",
        lineHeight: "30px !important"
    },
    cardDescription: {
        textAlign: "center",
        lineHeight: "35px",
        color: "grey !important"
    },
}));

function CourseCard(props) {
    const classes = useStyles();
    const { item, width, height, marginRight } = props;
    return (
        <Grid item className={classes.cardRoot} sx={{ width: `${width}px`, marginRight: `${marginRight}px` }} >
            <img src={item.imageSource} className={classes.cardImage} />
            <Typography variant="subtitle1" className={classes.cardTitle} >
                {item.name}
            </Typography>
            <Typography variant="body1" className={classes.cardDescription} >
                {item.platform}
            </Typography>
        </Grid >)
}

function Courses() {
    const classes = useStyles();
    const data = useSelector(state => state.courses.data);
    const isBelowMd = useMediaQuery(theme.breakpoints.down('md'));
    const isBelowLg = useMediaQuery(theme.breakpoints.down('lg'));
    return (
        <Grid container className={classes.root + " slideDownAnimation animated"} id="Courses">
            <Grid item xs={12}>
                <Typography variant="h2" className={classes.titleTexts}>
                    Courses
                </Typography>
            </Grid>
            <Grid item>
                <Typography variant="h6" className={classes.titleSubText}>
                    Unpurchased courses I have done that lack certificates.
                </Typography>
            </Grid>
            <Carousel
                CarouselCardElement={CourseCard}
                data={data}
                perIndexLength={isBelowMd ? 1 : isBelowLg ? 2 : 3}
                widthMultiplier={isBelowMd ? 1.0 : isBelowLg ? 0.475 : 0.325}
                marginRightMultiplier={isBelowMd ? 0.1 : isBelowLg ? 0.05 : 0.0125} />
        </ Grid >
    );
}

export default Courses;
