import React from 'react';

class PreloadImages extends React.Component {
  componentDidMount() {
    const imagesToPreload = [
        'media/intro/careercanvas.webp',
        'media/intro/discoverme.webp',
        'media/intro/explorationlab.webp',
        'media/intro/other.webp',
        'media/intro/summaryandreview.webp',
        'media/intro/welcome.webp',
      ];
  
      imagesToPreload.forEach(imagePath => {
        const img = new Image();
        img.src = imagePath;
      });
  }

  render() {
    return null; // PreloadImages component doesn't render anything
  }
}

export default PreloadImages;
