import React from 'react';
import { makeStyles } from '@mui/styles';
import { Grid, Typography } from '@mui/material';
import { LabelButton } from '../../utils/labelButton';
import { useDispatch } from 'react-redux';
import { LabelText } from '../../utils/labelText';

const useStyles = makeStyles((_) => ({
    labelContainer:{
        marginTop: "0px !important",
        marginBottom: "7.5px !important",
    }
}));

export function LabelItem(props) {
    const { labelsArray, colorMap } = props;
    const classes = useStyles();
    
    return (
        <Grid container spacing={1} className={classes.labelContainer} justifyContent={"center"}>
            {labelsArray.map((label, index) => {
                return(
                    <Grid item key={index}>
                        <LabelText color={colorMap.get(label)}>
                            {label}
                        </LabelText>
                    </Grid>)
                })
            }
        </Grid>
    );
}
