import { Typography, Grid } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { useRef, useLayoutEffect, useState, useEffect } from "react";
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import { SvgButton } from "./svgButtons";

const useStyles = makeStyles((_) => ({
    root: {
        width: "100%",
    },
    carousel: {
        width: "100%",
        position: "relative",
        overflow: "hidden",
    },
    navigationContainer: {
        paddingTop: "25px",
        marginRight: "auto",
        marginLeft: "auto",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        "& h6": {
            verticalAlign: "middle",
            marginLeft: "15px",
            marginRight: "15px"
        },
        "& div": {
            width: "50px",
            height: "50px"
        }
    },
    cardContainer: {
        whiteSpace: "nowrap",
        position: "relative",
        display: "block",
        transition: "left 0.4s",
        transitionTimingFunction: "ease-in-out"
    },
}));


export function Carousel(props) {
    const classes = useStyles();
    const {colorMap, rootClassName, carouselClassName, navigationClassName, CarouselCardElement, data, perIndexLength = 1, widthMultiplier = 1, heighMultiplier = 1, marginRightMultiplier = 0, NextButtonSvg = NavigateNextIcon, PreviousButtonSvg = NavigateBeforeIcon, hideNavigation } = props;
    const targetRef = useRef();
    const carouselLength = Math.ceil(data.length / perIndexLength) - 1
    const [dimension, setDimension] = useState({ width: 0, height: 0 });
    const [carouselIndex, setCarouselIndex] = useState(0);

    useLayoutEffect(() => {
        const handleResize = () => {
            setCarouselIndex(0);
            setDimension({
                width: targetRef.current.offsetWidth,
                height: targetRef.current.offsetHeight
            });
        };
    
        if (targetRef != null && targetRef.current) {
            setDimension({
                width: targetRef.current.offsetWidth,
                height: targetRef.current.offsetHeight
            });
    
            window.addEventListener('resize', handleResize);
    
            return () => {
                window.removeEventListener('resize', handleResize);
            };
        }
    }, [targetRef]);

    useEffect(() => {
        setCarouselIndex(0);
    }, [data]);
        
    const onNavigationButtonClick = (isNext) => {
        if (dimension.width === 0) {
            setDimension({
                width: targetRef.current.offsetWidth,
                height: targetRef.current.offsetHeight
            });
        }

        if (isNext && carouselIndex < carouselLength) {
            setCarouselIndex(carouselIndex + 1)
        }
        else if (!isNext && carouselIndex > 0) {
            setCarouselIndex(carouselIndex - 1)
        }
    }

    return (
        <div className={classes.root + " " + rootClassName}>
            <div ref={targetRef} className={classes.carousel + " " + carouselClassName}>
                <div className={classes.cardContainer} style={{ left: `${carouselIndex * -(dimension.width + marginRightMultiplier * dimension.width)}px` }}>
                    {
                        data.map((item, index) => {
                            return (
                                <CarouselCardElement
                                    key={index}
                                    item={item}
                                    colorMap={colorMap}
                                    style={{display: "none"}}
                                    width={dimension.width * widthMultiplier}
                                    height={dimension.height * heighMultiplier}
                                    marginRight={dimension.width * marginRightMultiplier}
                                />
                            )
                        })
                    }
                </div>
            </div>
            <div style={{ display: data.length === 0 ? "block" : "none" }}>
                <Typography variant="h6" display={"inline-block"}>
                    {"No matching items found. Please try different labels."}
                </Typography>
            </div >
            <div style={{ display: hideNavigation || data.length === 0 ? "none" : "flex" }} className={classes.navigationContainer + " " + navigationClassName}>
                <SvgButton idleColor="#03aee4" className="previousArrow" onClick={() => onNavigationButtonClick(false)}>
                    <PreviousButtonSvg />
                </SvgButton>
                <Typography variant="h6" display={"inline-block"}>
                    {(carouselIndex + 1) + "/" + (carouselLength + 1)}
                </Typography>
                <SvgButton idleColor="#03aee4" className="nextArrow" onClick={() => onNavigationButtonClick(true)}>
                    <NextButtonSvg />
                </SvgButton>
            </div >
        </div >
    );
}

