import React from 'react';
import { makeStyles } from '@mui/styles';
import { Typography } from '@mui/material';

const useStyles = makeStyles((theme) => ({
    svgButton: {
        display: "inline-block",
        borderRadius: "20px !important",
        position: "relative",
        padding: "2.5px 15px 2.5px 15px",
        marginRight: "10px",
        marginBottom: "25px",
        "& h6": {
            
            color: "inherit",
        },
        "&:hover": {
            cursor: "pointer",
        }
    },
}));


export function LabelButton(props) {
    const { id,className, onClick, children, idleColor, activeColor, isActive } = props;
    const colors= ["#202124", "white"]
    const [isHovered, setIsHovered] = React.useState(false);
    const classes = useStyles();
    const handleMouseEnter = () => {
        setIsHovered(true);
    };

    const handleMouseLeave = () => {
        setIsHovered(false);
    };

    const buttonStyle = {
        backgroundColor: isActive? activeColor:  colors[0],
        color: isActive? colors[1]: colors[1],
        cursor: "pointer",
        border:isActive?"2px solid "+colors[0]:"2px solid #62656c",
        transition: "background-color 0.3s, color 0.3s, border 0.3s",
        position: "relative",
    };

    if (isHovered ) {
        buttonStyle.backgroundColor =isActive? activeColor:colors[1];
        buttonStyle.color =isActive? colors[1]:colors[0];
    }

    return (
        <div
            id={id}
            style={buttonStyle}
            className={className + " " + classes.svgButton} 
            onClick={onClick}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
        >
            <Typography variant="subtitle1" >
                {children}
            </Typography>
        </div>
    );
}

