import Courses from "../archieved/courses";
import Intro from "./intro";
import Experience from "./cards/linkCard";
import { useSelector } from "react-redux";
import LinkCard from "./cards/linkCard";
import { makeStyles } from '@mui/styles';
import { Grid, Typography, Link, Tooltip, Box} from "@mui/material";
import theme from "../theme";
import { SvgButton } from "../utils/svgButtons";
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import { LabelMatcher } from "./partials/labelMatcher";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "75px 6vw 50px 6vw",
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
        paddingBottom: "25px",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },
    cardButton: {
        display:"flex",
        justifyContent: "center",
        marginTop:"7.5px !important",
        marginBottom:"5px !important",
        "& div": {
            width: "45px",
            height: "45px",
            marginBottom: "10px",
        },
        "& svg": {
            width: "25px",
            height: "25px"
        }
    },
}));

function DiscoverMe() {
    const classes = useStyles();
    const data = useSelector(state => state.discoverMe.data);
    const mainColor = useSelector(state => state.pallate.color);
    const activeLabels = useSelector(state => state.discoverMe.activeLabels);
    const availableLabels = useSelector(state => state.discoverMe.availableLabels);
    return (
        <div>
            <Intro  imagePath={"media/intro/discoverme.webp"} title={"Peek into My Personality!"} desc={"Delve into the depths of my personality, exploring my interests, preferences, and experiences that shape who I am."}/>
            <Grid container className={classes.root}>
                <LabelMatcher activeLabels={activeLabels} availableLabels={availableLabels} actionType={"update_discoverme_label"}/>
                <Grid container variant="row" spacing={2.5}>
                    {
                        data.map((item, index) => {
                            return (
                                <LinkCard
                                    key={index}
                                    imageSource={item.imageSource}
                                    title={item.title}
                                    date={item.date}
                                    labelsArray={item.labelsArray}
                                    colorMap={availableLabels}
                                    buttons={
                                    <Tooltip title="See on LinkedIn" key={index} >
                                        <Box>
                                            <SvgButton idleColor={mainColor} onClick={() => window.open(item.link, '_blank').focus()}>
                                                <LinkedInIcon />
                                            </SvgButton>
                                        </Box>
                                    </Tooltip>
                                    }
                                    />
                            )
                        })
                    }
                </Grid>
                <div style={{ display: data.length === 0 ? "block" : "none" }}>
                    <Typography variant="h6" display={"inline-block"}>
                        {"No matching items found. Please try different labels."}
                    </Typography>
                </div >
            </ Grid >
        </div>
    );
}

export default DiscoverMe;
