import { initialState as labelColor } from "./labelColorReducer";

const initialState = {
    data: [
        {
            name: "Cablenet Website",
            imageSources: [
                "media/websites/Cablenet Website 1.webp",
                "media/websites/Cablenet Website 2.webp",
                "media/websites/Cablenet Website 3.webp"
            ],
            labels: new Set(["Semisoft","Browser","ReactJS", "AWS"]),
            scopesOfWork: "Made ALL functionalities",
            date: "June 20, 2022",
            links: [{
                iconType: 0,
                link: "https://drive.google.com/file/d/1XhGEpNDExuw5sp1z7R-NKoKdJS51IUnh/view?usp=share_link"
            }]
        },
        {
            name: "Semisoft Website",
            imageSources: [
                "media/websites/Semisoft Website 1.webp",
                "media/websites/Semisoft Website 2.webp",
                "media/websites/Semisoft Website 3.webp"
            ],
            labels: new Set(["Semisoft","Browser","ReactJS", "AWS"]),
            scopesOfWork: "Made ALL functionalities",
            date: "May 23, 2022",
            links: [{
                iconType: 0,
                link: "https://drive.google.com/file/d/1Q7TcFw6HoNib_I7NagHk_4fAzNjIv-La/view?usp=share_link"
            }]
        },
    ],
    activeLabels: new Set(),
    availableLabels: new Map()
}


initialState.data.forEach(item => {
    item.labelsArray = [];
    item.labels.forEach(label => {
        item.labelsArray.push(label);
        if(!initialState.availableLabels.has(label)){
            const color= labelColor.data[initialState.availableLabels.size];
            initialState.availableLabels.set(label,color);
        }
    });
});


const websiteReducer = (state = initialState, action) => {
    switch (action.type) {
        case "update_website_label":
            const newActiveLabels = new Set(state.activeLabels);
            if (newActiveLabels.has(action.payload)) {
                newActiveLabels.delete(action.payload);
            } else {
                newActiveLabels.add(action.payload);
            }
            const newData = initialState.data.filter(item => {
                for (const label of newActiveLabels) {
                    if (!item.labels.has(label)) {
                        return false;
                    }
                }
                return true;
            });

            return {
                data: newData,
                activeLabels: newActiveLabels,
                availableLabels: initialState.availableLabels
            }
        default:
            return state;
    }
};

export default websiteReducer;
