import { Button, Grid, Link, Typography, Toolbar, AppBar, Slide, useScrollTrigger, useMediaQuery, alpha } from "@mui/material";
import { makeStyles } from '@mui/styles';
import theme from "../theme";
import { useEffect, useState } from "react";
import { useLayoutEffect } from "react";
import { useSelector } from "react-redux";
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import AutoAwesomeIcon from '@mui/icons-material/AutoAwesome';
import { useDispatch } from 'react-redux';
import { actions } from '../store/actions';
import { useLocation } from "react-router-dom";
import { Link as RouterLink } from "react-router-dom"; // Import RouterLink

const useStyles = makeStyles((_) => ({
    header:{
        backgroundColor: "rgba(32, 33, 36, 0.7) !important",
        backdropFilter: "blur(5px)", // Apply the backdrop filter to the container
    },
    titleText: {
        flexGrow: 1,
        fontWeight: "600 !important",
        [theme.breakpoints.down('lg')]: {
            fontSize: "24px !important",
            lineHeight: "1.3 !important",
            paddingTop: "10px",
            paddingBottom: "10px",
        }
    },
    navigationContainer: {
        paddingRight: "25px",
        "& *:hover": {
            cursor: "pointer",

        },
        "& *": {
            marginLeft: "25px !important"
        },
        [theme.breakpoints.down('lg')]: {
            paddingRight: "0px",
            "& *": {
                marginLeft: "0px !important"
            },
        }
    },
    imageDisplayer: {
        width: "100%",
        height: "100%",
        top: 0,
        left: 0,
        position: "fixed",
        zIndex: "1",
        display: "flex",
        "& img": {
            width: "80vw",
            height: "80vh",
            zIndex: "2 !important",
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%,-50%)",
            objectFit: "contain"
        },
        "& div": {
            opacity: "0.8",
            position: "absolute",
            width: "100%",
            height: "100%",
            top: 0,
            left: 0,
            zIndex: "1 !important"
        }
    },
}));

function AboveLgNavigation(props) {
    const { data } = props;
    return (
        <div>
            {data.map((item, index) => {
                    return (
                      <Link 
                      key={index} component={RouterLink} to={item.path} underline="hover" color={"white"}>
                        {item.title}
                      </Link>
                    );
                })
            }
        </div>)
}

function BelowLgNavigation(props) {
    const { data } = props;
    const [anchorElNav, setAnchorElNav] = useState(null);
    const handleOpenNavMenu = (event) => {
        setAnchorElNav(event.currentTarget);
    };
    const handleCloseNavMenu = () => {
        setAnchorElNav(null);
    };
    return (
        <div>
            <IconButton
                size="large"
                aria-controls="menu-appbar"
                aria-haspopup="true"
                onClick={handleOpenNavMenu}
                color="white"
            >
                <MenuIcon  sx={{color:"white"}}/>
            </IconButton>
            <Menu
                id="menu-appbar"
                anchorEl={anchorElNav}
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'left',
                }}
                keepMounted
                transformOrigin={{
                    vertical: 'top',
                    horizontal: 'left',
                }}
                open={Boolean(anchorElNav)}
                onClose={handleCloseNavMenu}
                
            >
                {data.map((item, index) => (
                    <MenuItem key={index} >
                        <Link key={index}  component={RouterLink} to={item.path} underline="hover" color={"black"}>
                            {item.title}
                        </Link>
                    </MenuItem>
                ))}
            </Menu>
        </div>
    )
}

function Header() {
    const classes = useStyles();
    const trigger = useScrollTrigger();
    const [isShowingImage, setIsShowingImage] = useState(false);
    const data = useSelector(state => state.header.data);
    const isBelowLg = useMediaQuery(theme.breakpoints.down('lg'));
    const [isBlackAndWhite, setIsBlackAndWhite] = useState(false);
    const mainColor = useSelector(state => state.pallate.color);
    const mainMessage = useSelector(state => state.pallate.title);
    const loc = useLocation();
    const dispatch = useDispatch(); // Get the dispatch function
    
    useLayoutEffect(() => {

        const elements = document.querySelectorAll('#CT');
      
        elements.forEach(element => {
          element.style.transition = 'none';
        });
      
        dispatch({
          type: "update_primary",
          payload: loc.pathname
        });
      
        setTimeout(() => {
          elements.forEach(element => {
            element.style.transition = 'color 1.7s';
          });
          window.scrollTo({ top: 0 }); // Use 'smooth' for smooth scrolling
        }, 10); // Use a small delay (in milliseconds) to ensure proper sequencing
      }, [loc.pathname]);
      
    

    useEffect(() => {
        const interval = setInterval(() => {
            setIsBlackAndWhite(prev => !prev); // Toggle the state
        }, 2000); // Change color every 1 second

        return () => clearInterval(interval); // Cleanup the interval on unmount
    }, []);

    return (
        <Grid container direction={"column"} className={classes.root} >
            <Slide appear={true} direction="down" in={!trigger && !isShowingImage}>
                <AppBar position="fixed" className={classes.header}>
                    <Toolbar>
                        <Button
                            size="large"
                            edge="start"
                            aria-label="menu"
                            sx={{ mr: 2 }}
                        >
                            <AutoAwesomeIcon id="CT" style={{color: isBlackAndWhite ? 'white' : mainColor, transitionTimingFunction: "ease-in-out"}} fontSize="large" />
                        </Button>
                        <Typography id="CT" style={{color: isBlackAndWhite ? 'white' : mainColor, transitionTimingFunction: "ease-in-out"}} variant="h6" component="div" className={classes.titleText}>
                            {mainMessage}
                        </Typography>
                        <div className={classes.navigationContainer}>
                            {
                                isBelowLg ?
                                    <BelowLgNavigation data={data} />
                                    : <AboveLgNavigation data={data} />
                            }
                        </div>
                    </Toolbar>
                </AppBar>
            </Slide>
        </Grid >
    );
}

export default Header;
