import { initialState as labelColor } from "./labelColorReducer";

const initialState = {
    data: [
        {
            imageSource: "media/summaryandreview/WebApplicationSoftwareArchitecture.webp",
            title: "SSDD (1/5): Web Application & Software Architecture",
            link: "https://www.linkedin.com/posts/lareza-farhan-wanaghi_summary-review-web-application-software-activity-7098147326803804160-5jh6?utm_source=share&utm_medium=member_desktop",
            date: "August 18, 2023",
            labels: new Set(["Architecture", "Learning Path"]),
        },
        {
            imageSource: "media/summaryandreview/MoreEffectiveAgile.webp",
            title: "More Effective Agile: A Roadmap for Software Leaders",
            link: "https://www.linkedin.com/posts/lareza-farhan-wanaghi_summary-review-more-effective-agile-activity-7091962580491005952-enGT?utm_source=share&utm_medium=member_desktop",
            date: "August 1, 2023",
            labels: new Set(["Leadership"]),
        },
    ],
    activeLabels: new Set(),
    availableLabels:new Map()
}

initialState.data.forEach(item => {
    item.labelsArray = [];
    item.labels.forEach(label => {
        item.labelsArray.push(label);
        if(!initialState.availableLabels.has(label)){
            const color= labelColor.data[initialState.availableLabels.size];
            initialState.availableLabels.set(label,color);
        }
    });
});

const summaryAndReviewReducer = (state = initialState, action) => {
    switch (action.type) {
        case "update_summaryandreview_label":
            const newActiveLabels = new Set(state.activeLabels);
            if (newActiveLabels.has(action.payload)) {
                newActiveLabels.delete(action.payload);
            } else {
                newActiveLabels.add(action.payload);
            }
            const newData = initialState.data.filter(item => {
                for (const label of newActiveLabels) {
                    if (!item.labels.has(label)) {
                        return false;
                    }
                }
                return true;
            });

            return {
                data: newData,
                activeLabels: newActiveLabels,
                availableLabels: initialState.availableLabels
            }
        default:
            return state;
    }
};

export default summaryAndReviewReducer;