import React from 'react';
import { makeStyles } from '@mui/styles';
import { Grid, Typography } from '@mui/material';
import { LabelButton } from '../../utils/labelButton';
import { useDispatch } from 'react-redux';

const useStyles = makeStyles((theme) => ({
   
}));


export function LabelMatcher(props) {
    const { availableLabels, activeLabels, actionType } = props;
    const classes = useStyles();
    const dispatch = useDispatch(); // Get the dispatch function

    const handleGamesLabelUpdate = (label) =>()=> {
        dispatch({ type: actionType, payload: label });
    };

    return (
        <Grid item xs={12}>
            {Array.from(availableLabels.entries()).map(([label, labelColor], index) => (
                <LabelButton isActive={activeLabels.has(label)} activeColor={labelColor} key={index} onClick={handleGamesLabelUpdate(label)}>
                    {`${label}`}
                </LabelButton>
            ))}
        </Grid>
    );
}