// themeReducer.js
import { initialState as headerInitialState } from "./headerReducer";

// Define initial state
const initialState = {
  title:"Home",
  path:"/",
  color:"#78ccbf"
};
  
  // Define reducer
  const pallateReducer = (state = initialState, action) => {
    switch (action.type) {
      case 'update_primary':
        const foundItem = headerInitialState.data.find(item => item.path === action.payload);
        return foundItem
      default:
        return state;
    }
  };
  
  export default pallateReducer;
  