import { combineReducers } from 'redux'
import coursesReducer from './coursesReducer';
import educationReducer from './educationReducer';
import headerReducer from './headerReducer';
import recentActivitiesReducer from './recentActivitiesReducer';
import pallateReducer from './pallateReducer';
import summaryAndReviewReducer from './summaryAndReviewReducer';
import explorationLabReducer from './explorationLabReducer';
import discoverMeReducer from './discoverMeReducer';
import websiteReducer from './websiteReducer';
import gamesReducer from './gamesReducer';
import milestoneReducer from './milestoneReducer';
import labelColorReducer from './labelColorReducer';

const reducers = combineReducers({
    courses: coursesReducer,
    education: educationReducer,
    summaryAndReview: summaryAndReviewReducer,
    games: gamesReducer,
    website: websiteReducer,
    header: headerReducer,
    recentActivities: recentActivitiesReducer,
    pallate: pallateReducer,
    explorationLab: explorationLabReducer,
    milestone: milestoneReducer,
    discoverMe: discoverMeReducer,
    labelColor: labelColorReducer
});

export default reducers;