import { Grid, List, ListItem, ListItemText, Tooltip, Typography, } from "@mui/material";
import { makeStyles } from '@mui/styles';
import { Carousel } from "../../utils/carousels";
import { useDispatch, useSelector } from "react-redux";
import ArrowLeftIcon from '@mui/icons-material/ArrowLeft';
import ArrowRightIcon from '@mui/icons-material/ArrowRight';
import theme from "../../theme";
import useMediaQuery from '@mui/material/useMediaQuery';
import { SvgButton } from "../../utils/svgButtons";
import OndemandVideoIcon from '@mui/icons-material/OndemandVideo';
import { Box } from "@mui/system";
import ProjectCard from "./projectCard";
import { LabelButton } from "../../utils/labelButton";
import { LabelMatcher } from "../partials/labelMatcher";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "75px 6vw 25px 6vw",
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
        paddingBottom: "25px",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },
}));

function Games() {
    const classes = useStyles();
    const data = useSelector(state => state.games.data);
    const activeLabels = useSelector(state => state.games.activeLabels);
    const availableLabels = useSelector(state => state.games.availableLabels);
    const isBelowMd = useMediaQuery(theme.breakpoints.down('md'));
    const isBelowLg = useMediaQuery(theme.breakpoints.down('lg'));

    return (
        <Grid  container className={classes.root + " slideRightAnimation animated"} id="Projects">
            <Grid item xs={12}>
                <Typography variant="h2" className={classes.titleTexts}>
                    Games
                </Typography>
            </Grid>

            <LabelMatcher activeLabels={activeLabels} availableLabels={availableLabels} actionType={"update_games_label"}/>

            <Carousel
                colorMap={availableLabels}
                CarouselCardElement={ProjectCard}
                data={data}
                perIndexLength={isBelowMd ? 1 : isBelowLg ? 2 : 3}
                widthMultiplier={isBelowMd ? 1.0 : isBelowLg ? 0.475 : 0.3225}
                marginRightMultiplier={isBelowMd ? 0.1 : isBelowLg ? 0.05 : 0.01625} />

        </ Grid >
    );
}

export default Games;
