import Projects from "./cards/games";
import Courses from "../archieved/courses";
import Intro from "./intro";
import Experience from "./cards/linkCard";
import { useSelector } from "react-redux";
import LinkCard from "./cards/linkCard";
import { makeStyles } from '@mui/styles';
import { Grid, Typography, Link, Tooltip, Box} from "@mui/material";
import theme from "../theme";
import { SvgButton } from "../utils/svgButtons";
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import MilestoneCard from "./cards/milestoneCard";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "50px 6vw 25px 6vw", // -25px
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
        paddingBottom: "25px",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },

}));

function Milestone() {
    const classes = useStyles();
    const data = useSelector(state => state.milestone.data);
    const mainColor = useSelector(state => state.pallate.color);

    return (
        <div>
            <Intro  imagePath={"media/intro/milestone.webp"} title={"Embark on the Journey!"} desc={"Navigate through the evolution of my career, exploring pivotal landmarks, personal growth, and the pursuit of aspirations that have left enduring marks."}/>
            <Grid container className={classes.root}>
                <Grid container variant="row">
                    {
                        data.map((item, index) => {
                            return (
                                <MilestoneCard
                                    hasPrev={index!=0}
                                    hasNext={index!=data.length-1}
                                    isLeft={!item.isImportant}
                                    isBig={item.isImportant}
                                    key={index}
                                    imageSource={item.imageSource}
                                    title={item.title}
                                    startDate={item.startDate}
                                    endDate={item.endDate}
                                    style={{ order: data.length - index }}
                                    mainColor={mainColor}
                                    buttons={item.links.length === 0?null:
                                    <Grid  container  id="content-button">
                                        {item.links.map((item2,index2) => {
                                            return(
                                            <Grid id="content-button-child"  item  key={index2}>
                                                <Grid justifyContent={index%2===0?"right":"left"}   container>
                                                    <Grid  item >
                                                        <Tooltip title="See on LinkedIn"  placement="top">
                                                            <Box id="link-svg-box" >
                                                                <SvgButton id="link-svg-button" activeColor="gray" idleColor={mainColor} onClick={() => window.open(item2.link, '_blank').focus()}>
                                                                    <LinkedInIcon />
                                                                </SvgButton>
                                                            </Box>
                                                        </Tooltip>
                                                    </Grid>
                                                    <Grid  item>
                                                        <Typography id="link-message" variant="subtitle2" >
                                                            {item2.message}
                                                        </Typography>
                                                    </Grid>
                                                </Grid>
                                            </Grid>
                                            )})
                                        }
                                    </Grid>}
                                    />
                            )
                        })
                    }
                </Grid>
            </ Grid >
        </div>
    );
}

export default Milestone;
