import Intro from "./intro";
import { useSelector } from "react-redux";
import LinkCard from "./cards/linkCard";
import { makeStyles } from '@mui/styles';
import { Grid, Typography, Link, Tooltip, Box} from "@mui/material";
import theme from "../theme";
import { SvgButton } from "../utils/svgButtons";
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import { LabelMatcher } from "./partials/labelMatcher";

const useStyles = makeStyles((_) => ({
    root: {
        padding: "75px 6vw 50px 6vw",
        [theme.breakpoints.down('md')]: {
            justifyContent: "center"
        }
    },
    titleTexts: {
        fontWeight: "bold !important",
        paddingBottom: "25px",
        [theme.breakpoints.down('md')]: {
            textAlign: "center"
        }
    },
    cardButton: {
        display:"flex",
        justifyContent: "center",
        marginTop:"7.5px !important",
        marginBottom:"5px !important",
        marginLeft: "10px" ,
        marginRight: "10px" ,
        "& div": {
            width: "45px",
            height: "45px",
            marginBottom: "10px",
        },
        "& svg": {
            width: "25px",
            height: "25px"
        }
    },
    buttonContainer:{
        width:"100% !important",
        display: "flex",
        justifyContent: "center",
        gap: "10px" // Adjust the value as needed for the desired spacing
        
    }
}));

function ExplorationLab() {
    const classes = useStyles();
    const data = useSelector(state => state.explorationLab.data);
    const mainColor = useSelector(state => state.pallate.color);
    const activeLabels = useSelector(state => state.explorationLab.activeLabels);
    const availableLabels = useSelector(state => state.explorationLab.availableLabels);
    return (
        <div>
            <Intro title={"Get Hands-on!"} desc={"Join me in unveiling an array of experimental projects while delving into the intricacies of diverse subjects, fostering hands-on learning and creative exploration."} imagePath={"media/intro/explorationlab.webp"}/>
            <Grid container className={classes.root}>
                <LabelMatcher activeLabels={activeLabels} availableLabels={availableLabels} actionType={"update_explorationlab_label"}/>
                <Grid container variant="row" spacing={2.5}>
                    {
                        data.map((item, index) => {
                            return (
                                <LinkCard
                                    key={index}
                                    imageSource={item.imageSource}
                                    title={item.title}
                                    date={item.date}
                                    labelsArray={item.labelsArray}
                                    colorMap={availableLabels}
                                    buttons={
                                    <div  key={index} className={classes.buttonContainer}>
                                    <Tooltip title="See on LinkedIn"  >
                                        <Box>
                                            <SvgButton idleColor={mainColor} onClick={() => window.open(item.linkedInLink, '_blank').focus()}>
                                                <LinkedInIcon />
                                            </SvgButton>
                                        </Box>
                                    </Tooltip>
                                    {item.githubLink === ""?null:<Tooltip title="See on GitHub"  >
                                        <Box>
                                            <SvgButton idleColor={mainColor} onClick={() => window.open(item.githubLink, '_blank').focus()}>
                                                <GitHubIcon />
                                            </SvgButton>
                                        </Box>
                                    </Tooltip>}
                                    </div>
                                    }
                                />
                            )
                        })
                    }
                </Grid>
                <div style={{ display: data.length === 0 ? "block" : "none" }}>
                    <Typography variant="h6" display={"inline-block"}>
                        {"No matching items found. Please try different labels."}
                    </Typography>
                </div >
            </ Grid >
        </div>
    );
}

export default ExplorationLab;
