function ProjectCardImage(props) {
    const classes = useStyles();
    const { item, width, height, marginRight } = props;
    return (
        <Grid item className={classes.cardImageCard} sx={{ width: `${width}px`, height: height, marginRight: `${marginRight}px` }} >
            <img src={item} />
        </Grid >)
}

function ProjectCard(props) {
    const classes = useStyles();
    const { item, width, marginRight, colorMap } = props;
    return (
        <Grid item className={classes.card} sx={{ width: `${width}px`, marginRight: `${marginRight}px` }} >
            <Carousel
                rootClassName={classes.cardImageCarouselRoot}
                carouselClassName={classes.cardImageCarousel}
                navigationClassName={classes.cardImageNavigation}
                CarouselCardElement={ProjectCardImage}
                data={item.imageSources}
                perIndexLength={1}
                NextButtonSvg={ArrowRightIcon}
                PreviousButtonSvg={ArrowLeftIcon}
                widthMultiplier={1}
                heighMultiplier={0.9}
                marginRightMultiplier={0.03}
            />
            <Typography variant="subtitle1" className={classes.cardTitle} >
                {item.name}
            </Typography>
            <LabelItem labelsArray={item.labelsArray} colorMap={colorMap}/>
            <Typography variant="subtitle2" color={"grey"} >
                {item.date}
            </Typography>
            <Grid container className={classes.linksContainer}>
                {item.links.map((link, index) => {
                    return (
                        link.iconType === 0?
                        <Tooltip title="Watch showcase video" key={index} >
                            <Box>
                                <SvgButton idleColor="#03aee4" onClick={() => window.open(link.link, '_blank').focus()}>
                                    <OndemandVideoIcon />
                                </SvgButton>
                            </Box>
                        </Tooltip>:
                        <Tooltip title="Play demo" key={index} >
                            <Box>
                                <SvgButton idleColor="#03aee4" onClick={() => window.open(link.link, '_blank').focus()}>
                                    <SportsEsportsIcon />
                                </SvgButton>
                            </Box>
                        </Tooltip>
                    )
                })}
            </Grid>
      
        </Grid >)
}


export default ProjectCard;
